.control-wrapper {
    width: 60%;
    margin: 0px;
    align-items: center;
    justify-content: space-between;
}

.action-btn {
    width: 50px;
    height: 50px;
    border-radius: 20px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2 ease-in-out;
}

.action-btn:hover{
    transform: scale(1.1);
}

.play-pause-btn {
    width: 65px;
    height: 65px;
    border-radius: 30px;
    background-color: #278e4d;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2 ease-in-out;
    box-shadow: inset -4px -4px 10px #1e4922, inset 4px 4px 10px 10px #63c662;
}

.play-pause-btn.active {
    box-shadow: inset -4px -4px 10px #1e4922, inset 4px 4px 10px 10px #63c662;
}

.play-pause-btn:hover {
    transform: scale(1.1);;
}