.library-body{
    width: 90%;
    height: 90%;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    justify-content: space-between;
}

.playlist-card{
    position: relative;
    width: 20%;
    height: 42%;
    border-radius: 20px;
    background-color:rgb(33, 49, 34);
    border: 1px solid rgba(54,69,98,0.18);
    padding: 1%;
    margin-bottom: 2%;
    background: rgb(49,73,51);
    background: linear-gradient(90deg, rgba(49,73,51,1) 32%, rgba(59,147,91,1) 100%);  
    transition: all 0.2s ease;
    cursor: pointer;
}

.playlist-card:hover {
    transform: scale(1.02);
}

.playlist-card:hover .playlist-fade{
    opacity: 1;
}

.playlist-image{
    width: 100%;
    aspect-ratio: 1;
    /* height: 77%; */
    border-radius: 15px;
}

.playlist-title {
    font-weight: 800;
    font-size: 16px;
    /* color: #79F877 */
    color: #CCFCCB;
    margin: 5px 0px 5px;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}

.playlist-subtitle {
    font-weight: 400;
    font-size: 14px;
    margin: 2px;
    /* color: #CCFCCB; */
    color: #A0FA9E;
}

.playlist-fade{
    position: absolute;
    right: 0;
    bottom: 0;
    opacity: 0;
    width: 84%;
    height: 34%;
    border-radius: 20px;
    display: flex;
    align-items: end;
    justify-content: flex-end;
    padding: 6%;
    transition: 0.3s ease;
}

@media (max-width: 1200px) {
    .playlist-card {
        width: 30%; /* Adjust width for medium screens */
    }

    .playlist-fade{
        position: absolute;
        right: 0;
        bottom: 0;
        opacity: 0;
        width: 84%;
        height: 34%;
        border-radius: 20px;
        display: flex;
        align-items: end;
        justify-content: flex-end;
        padding: 6%;
        transition: 0.3s ease;
    }

    .playlist-card:hover .playlist-fade{
        opacity: 1;
    }
}

@media (max-width: 900px) {
    .playlist-card {
        width: 45%; /* Adjust width for smaller screens */
    }
    .library-body{
        width: 95%;
        height: 95%;
    }

}

@media (max-width: 600px) {
    .playlist-card {
        width: 100%; /* Full width for mobile devices */
        height: auto; /* Adjust height for better display */
    }
    .library-body{
        width: 95%;
        height: 95%;
    }

    @media (max-width: 600px) {
        .playlist-card {
            width: 100%; /* Full width for mobile */
            height: auto;
            position: relative;
        }
    
        .playlist-fade{
            position: absolute;
            right: -1%;
            bottom: -5%;
            opacity: 1;
            width: 84%;
            height: 34%;
            border-radius: 20px;
            display: flex;
            align-items: end;
            justify-content: flex-end;
            padding: 6%;
            transition: 0.3s ease;
        }
    }
    
}